<template>
  <div class="page-login">
    <van-cell-group title="">
      <van-field label-width="110" v-model="mobile" clearable label="" placeholder="">
        <van-button slot="button" size="small" plain type="info" @click="sendCheckCode"
          ></van-button
        >
      </van-field>
      <van-field
        label-width="110"
        v-model="checkCode"
        clearable
        label=""
        placeholder="6"
      />
    </van-cell-group>
    <div class="bottom">
      <van-button type="info" @click="commit"></van-button>
    </div>
  </div>
</template>

<script>
import Vue from 'vue'
import { mapState, mapActions } from 'vuex'
import qs from 'querystring'
import * as dd from 'dingtalk-jsapi'
import { CellGroup, Field, Button, Toast, Dialog } from 'vant'
import { valiMobile } from '@/utils/validate'
import request from '@/utils/request'
import { setToken } from '@/utils/token'

Vue.use(Field)
  .use(CellGroup)
  .use(Button)
  .use(Toast)
  .use(Dialog)

dd.ready(function() {
  // dd.readyjsapi
  dd.runtime.permission.requestAuthCode({
    corpId: 'corpid',
    onSuccess: function(result) {
      console.log(result)
    },
    onFail: function(err) {
      console.log(err)
    }
  })
})

export default {
  name: 'login',
  components: {},
  data() {
    return {
      mobile: '',
      checkCode: ''
    }
  },
  computed: {},
  created() {},
  methods: {
    ...mapActions('login', ['getLoginUserInfo']),
    async sendCheckCode() {
      if (!valiMobile(this.mobile)) {
        Toast.fail('')
        return
      }
      const params = {
        mobile: this.mobile
      }
      const response = await request(`/checkCode/sendPhoneMsgCheckCode?${qs.stringify(params)}`, {
        method: 'POST'
      })
      if (response.resultCode === 200) {
        Toast.success(response.message)
      } else {
        Toast.fail(response.message)
      }
    },
    valid() {
      const { mobile, checkCode } = this

      if (!valiMobile(mobile)) {
        Toast({ type: 'fail', message: '\n', duration: 800 })
        return false
      }
      if (checkCode.length !== 6) {
        Toast({ type: 'fail', message: '\n6', duration: 800 })
        return false
      }

      return true
    },
    async commit() {
      if (this.valid()) {
        const params = {
          mobile: this.mobile,
          checkCode: this.checkCode
        }
        const response = await request(`/auth/fujingLoginByPhoneCheckCode?${qs.stringify(params)}`, {
          method: 'POST'
        })
        if (response.resultCode === 200) {
          setToken(response.data.token)
          Toast.success({ message: response.message, duration: 600 })
          this.getLoginUserInfo()
          setTimeout(() => {
            this.$router.push({
              name: 'punish'
            })
          }, 500)
        } else {
          Toast.fail(response.message)
        }
      }
    }
  }
}
</script>

<style lang="less" scoped>
.bottom {
  margin-top: 100px;
  width: 10rem;
  text-align: center;
  .van-button {
    width: 70%;
  }
}
</style>
